export default {
	data() {
		return {
			array:[],
			index:0,
			free:this.$expressCost,
		};
	},
	onLoad:function(){
		this.getCost();
	},
	methods:{
		getCost:function(){
			uni.request({
				url:this.$url+'getExpressCost',
				success: (res) => {
					console.log(res)
					this.array=res.data
				}
			})
		},
		setCost:function(){
			var cost=[]
			for(var i in this.array){
				cost.push(this.array[i].cost)
			}
			uni.request({
				url:this.$url+'setExpressCost',
				data:{cost:cost.join(',')},
				success: (res) => {
					uni.showToast({title:'修改成功'})
					this.array=res.data
				}
			})
		}
	}
}
